"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Mic, MicOff, Volume2 } from "lucide-react"

export function VoiceAssistant() {
  const [isListening, setIsListening] = useState(false)
  const [lastCommand, setLastCommand] = useState<string>("")
  const [feedback, setFeedback] = useState<string>("")
  const [isSupported, setIsSupported] = useState(true)

  useEffect(() => {
    // Check if browser supports Web Speech API
    if (typeof window !== "undefined") {
      setIsSupported("webkitSpeechRecognition" in window || "SpeechRecognition" in window)
    }
  }, [])

  const speak = (text: string) => {
    if (typeof window !== "undefined" && "speechSynthesis" in window) {
      const utterance = new SpeechSynthesisUtterance(text)
      utterance.lang = "en-MY"
      utterance.rate = 0.9
      window.speechSynthesis.speak(utterance)
    }
  }

  const startListening = () => {
    if (!isSupported) {
      setFeedback("Voice commands are not supported in this browser")
      return
    }

    setIsListening(true)
    setFeedback("Listening...")
    speak("Listening for command")

    // Simulate voice recognition (in production, use actual Web Speech API)
    setTimeout(() => {
      const commands = [
        "Go to Personal",
        "Open Finance",
        "Show Driving License",
        "View Medical Card",
        "Pay Property Bills",
        "Check Employment",
        "Family Proxy",
      ]
      const randomCommand = commands[Math.floor(Math.random() * commands.length)]
      setLastCommand(randomCommand)
      setFeedback(`Command: ${randomCommand}`)
      speak(`Opening ${randomCommand}`)
      setIsListening(false)
    }, 2000)
  }

  const stopListening = () => {
    setIsListening(false)
    setFeedback("")
    speak("Stopped listening")
  }

  if (!isSupported) {
    return null
  }

  return (
    <Card className="fixed bottom-6 right-6 w-80 shadow-lg z-50">
      <CardContent className="p-6">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-2">
            <Volume2 className="h-6 w-6 text-primary" />
            <h3 className="text-xl font-bold">Voice Assistant</h3>
          </div>
          <Button
            size="lg"
            variant={isListening ? "destructive" : "default"}
            className="h-14 w-14 rounded-full"
            onClick={isListening ? stopListening : startListening}
            aria-label={isListening ? "Stop listening" : "Start voice command"}
          >
            {isListening ? <MicOff className="h-6 w-6" /> : <Mic className="h-6 w-6" />}
          </Button>
        </div>

        {feedback && (
          <div className="mb-2">
            <p className="text-base text-muted-foreground">{feedback}</p>
          </div>
        )}

        {lastCommand && !isListening && (
          <div className="mt-2 p-3 bg-muted rounded-lg">
            <p className="text-sm text-muted-foreground mb-1">Last Command:</p>
            <p className="text-lg font-semibold">{lastCommand}</p>
          </div>
        )}

        <p className="text-sm text-muted-foreground mt-4">
          Click the microphone to give voice commands like "Go to Personal" or "Open Finance"
        </p>
      </CardContent>
    </Card>
  )
}
