"use client"

import { Button } from "@/components/ui/button"
import { Settings, LogOut, Menu } from "lucide-react"
import Link from "next/link"
import Image from "next/image"
import { useState } from "react"
import { useRouter } from "next/navigation"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"

interface DashboardHeaderProps {
  userName: string
  userId: string
}

export function DashboardHeader({ userName, userId }: DashboardHeaderProps) {
  const [isLoading, setIsLoading] = useState(false)
  const router = useRouter()

  const handleSignOut = async () => {
    setIsLoading(true)
    setTimeout(() => {
      router.push("/")
    }, 500)
  }

  return (
    <header className="border-b bg-card">
      <div className="container mx-auto flex h-20 items-center justify-between px-4 md:px-6">
        <div className="flex items-center gap-4">
          <Link href="/dashboard" className="text-2xl font-bold hover:text-primary md:text-3xl">
             <Image src="/MyKITA.png" alt="MyKITA" width={100} height={100} className="object-contain" />
          </Link>
        </div>

        <div className="flex items-center gap-4">
          <span className="hidden text-lg font-medium md:inline-block">Hello, {userName}</span>

          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button
                variant="outline"
                size="lg"
                className="h-12 gap-2 bg-transparent"
                aria-label="Open user menu"
                aria-haspopup="true"
              >
                <Menu className="h-5 w-5" aria-hidden="true" />
                <span className="sr-only">Open menu</span>
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end" className="w-56">
              <div className="px-2 py-2 md:hidden">
                <p className="text-base font-medium">{userName}</p>
              </div>
              <DropdownMenuSeparator className="md:hidden" />
              <DropdownMenuItem asChild>
                <Link href="/settings" className="flex h-12 cursor-pointer items-center gap-3 text-base">
                  <Settings className="h-5 w-5" aria-hidden="true" />
                  Settings
                </Link>
              </DropdownMenuItem>
              <DropdownMenuSeparator />
              <DropdownMenuItem
                onClick={handleSignOut}
                disabled={isLoading}
                className="flex h-12 cursor-pointer items-center gap-3 text-base text-red-600 focus:text-red-600"
              >
                <LogOut className="h-5 w-5" aria-hidden="true" />
                {isLoading ? "Signing out..." : "Sign Out"}
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>
    </header>
  )
}
